.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
  font-size: 20px;

  .checkout-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgray;
    padding: 10px 0;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }
  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
  .test-warning {
    text-align: center;
    color: red;
    font-size: 24px;
  }
  button {
    margin: 20px 0;
  }
}
